html {
    box-sizing: border-box;
    scroll-behavior: smooth;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    font-family: "Intro", sans-serif;
    background-color: $color-white;
}

img {
    max-width: 100%;
}

a {
    text-decoration: none;
}

.list-reset {
    list-style: none;
    padding: 0;
    margin: 0;
}

.is-outer {
    position: relative !important; // stylelint-disable-line declaration-no-important
    left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
    overflow: hidden;
}

.is-hidden {
    display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
    display: none;

    @include mobile {
        display: block;
    }
}

@import "mixins";

.error {
    border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
    padding: 0;
    outline: none;
    border: none;
    background: transparent;
    cursor: pointer;
}

.container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1210px;
}

.js-focus-visible :focus:not(.focus-visible) {
    outline: none;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0);
}

.logo {
    font: $font-xl;
    line-height: 1;
    color: $color-white;
}

.grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $grid-gap;

    @include mobile {
        grid-template-columns: repeat(4, 1fr);
    }
}

.btn {
    border-radius: 50px;
    padding: 22px 47px;
    font: $font-s-bold;
    outline: none;
    transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

    &--primary {
        background-color: $color-amber;
        color: $color-white;

        @include desktop {
            &:hover {
                background-color: $color-amber-hover;
            }
        }

        &:active {
            background-color: $color-amber-active;
        }
    }

    &--secondary {
        padding: 17px 42px;
        border: 1px solid $color-amber;
        color: $color-amber;
        background-color: transparent;

        @include desktop {
            &:hover {
                color: $color-white;
                background-color: $color-amber-hover;
            }
        }

        &:active {
            background-color: $color-amber-active;
        }
    }
}

.btn--primary.focus-visible {
    background-color: $color-amber-hover;
}

.btn--secondary.focus-visible {
    background-color: $color-amber-hover;
}

.title {
    font: $font-xl;
    color: $color-white;
}

.custom-checkbox {
    @include custom-checkbox;

    &__content {
        padding-left: 35px;
        font: $font-s;
        color: $color-white;
        &::before {
            top: -2px;
            border-color: $color-amber;
            border-radius: 4px;
            width: 24px;
            height: 24px;
        }

        &::after {
            width: 24px;
            height: 24px;
            background-image: url("../img/check.svg");
            background-size: 15px 12px;
            fill: $color-amber;
        }
    }

    .custom-checkbox__field:focus + .custom-checkbox__content::before {
        outline: 2px solid $color-white;
    }
}

.tooltip {
    position: relative;
    margin-left: $offset-xs;

    @include mobile {
        margin-left: 0;
        margin-right: $offset-xs;
    }
}

.tooltip__button {
    border-radius: 100%;
    width: 20px;
    height: 20px;
    margin-bottom: 30%;
    background-color: $color-amber;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    box-shadow: 2px 4px 10px rgba(0, 13, 84, 0.2);
    svg {
        width: 6px;
        height: 10px;
    }
}

.tooltip__text {
    position: absolute;
    left: -10px;
    bottom: calc(100% + 14px);
    z-index: 3;
    border-radius: 4px;
    padding: $offset-xs $offset-m;
    width: max-content;
    max-width: 426px;
    box-shadow: 4px 8px 20px rgba(0, 13, 84, 0.2);
    font: $font-xs;
    text-transform: initial;
    color: $color-white;
    background-color: rgba($color: $color-midnight-blue, $alpha: 0.8);
    opacity: 0;
    visibility: hidden;
    transform: translateY(10px);
    transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out, visibility 0.3s ease-in-out;

    @include mobile {
        max-width: 310px;
        font-size: 13px;
    }
}

.tooltip__text::after {
    content: "";
    position: absolute;
    left: 10px;
    bottom: -8px;
    border-style: solid;
    border-width: 10px 8px 0;
    border-color: $color-midnight-blue transparent transparent transparent;
    border-radius: 4px;
    width: 0;
    height: 0;
}

.tooltip__button:hover + .tooltip__text {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}

.tooltip__button:focus + .tooltip__text {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}

.input {
    display: block;
    border: none;
    padding: 0 $offset-m;
    height: 60px;
    border-radius: 30px;
    background-color: $color-white;
}

/*
 *  STYLE 3
 */

body::-webkit-scrollbar-track {
    background-color: transparent;
    opacity: 0;
}

body::-webkit-scrollbar {
    width: 12px;
    background-color: #1f3855;
}

body::-webkit-scrollbar-thumb {
    background-color: $color-amber;
    border-radius: 30px;
    border: 3px solid #1f3855;
    padding: 0 2px;
}
